@include tablet {
    .iconos-nosotros {
        @include grid(3, 2rem);
    }
}

.icono,
.iconos {
    text-align: center;

    img {
        height: 10rem;
        width: 10rem;
        display: block;
        margin: 0 auto;
    }

    h3 {
        text-transform: uppercase;
    }
}

.iconos {
    img {
        height: 20rem;
        width: 25rem;
    }
}

.informacion-metas span {

    color: $amarillo;

    @include tablet {
        @include grid(2, 1rem);
        grid-auto-flow: column;
    }

    a {
        text-decoration: none;
        color: $grisOscuro;
    }
}

.icono-contacto {
    width: 5rem;
}